import {Component} from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {LiveProvider} from "../../providers/live/notice-provider";


@IonicPage()
@Component({
  selector: 'page-live',
  templateUrl: 'live.html',
})
export class LivePage {
  private livedata: any;
  private bid: any = "1";
  private loading: any;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private live : LiveProvider,
              private  loadingCtrl: LoadingController
              ) {
    
this.loadData()
  }


  ionViewDidLoad() {

  }

  async loadData(){
      const loader = this.loadingCtrl.create({
          content: 'Please wait...',
          duration: 10000
      })
      loader.present()
    this.live.query(this.bid).then((res: any) => {
        this.loading.dismissAll()
      this.livedata = JSON.parse(res.data)
        console.log("ododoodfodfd: "+ this.livedata)


    }).catch(er=> {
      //this.loading.dismiss()
  //  loader.dismissAll()
      console.log(JSON.stringify(er))})
    ;
  }

  goToLive(data)
  {
    this.navCtrl.push("VideoPlayer",{
      id:data.identification,
        info:data
    })
  }




}



